








































import Vue from "vue";
import Component from "vue-class-component";
import * as api from "../utils/api";
import * as store from "../plugins/store";
import { i18n } from "../plugins/i18n";

@Component
export default class Wit extends Vue {
  key: File | null = null;
  file: File | null = null;

  loading = false;
  recognizing = false;
  progress = 0;

  status = "";

  result = "";

  async recognize() {
    if (!this.key || !this.file) {
      return;
    }
    this.loading = true;
    this.progress = 0;
    this.status = i18n.t("uploading") as string;
    try {
      const response = await api.recognize(
        "google",
        this.key,
        this.file!,
        progress => {
          this.progress = progress;
          this.status = `${i18n.t("uploading")} (${progress}%)`;
          if (this.progress === 100) {
            this.recognizing = true;
            this.status = i18n.t("recognizing") as string;
          }
        }
      );
      this.result = response.data.text || i18n.t("noText");
    } catch (err) {
      store.setSnackbar({
        message: err.message,
        active: true,
        color: "error"
      });
    } finally {
      this.loading = false;
      this.recognizing = false;
    }
  }
}
